import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import ExchangeLonless from "../views/ExchangeLonless.vue";
import Miscellanea from "../views/Miscellanea.vue";
import Mould from "../views/Mould.vue";
import PetriDish from "../views/PetriDish.vue";
import Regeneration from "../views/Regeneration.vue";
import Biography from "../views/Biography.vue";
import Contact from "../views/Contact.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/exchange_loneliness",
    name: "exchange_loneliness",
    component: ExchangeLonless
  },
  {
    path: "/miscellanea",
    name: "miscellanea",
    component: Miscellanea
  },
  {
    path: "/mould",
    name: "mould",
    component: Mould
  },
  {
    path: "/petri_dish",
    name: "petri_dish",
    component: PetriDish
  },
  {
    path: "/regeneration",
    name: "regeneration",
    component: Regeneration
  },
  {
    path: "/biography",
    name: "biography",
    component: Biography
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact
  },
];

const router = new VueRouter({
  routes
});

export default router;
